.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 45px 16px 46px;
}

.text:nth-child(2) {
  margin-bottom: 15px;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.23;
  text-align: center;
  color: var(--primary-text-color);
}

hr {
  width: 100px;
  border: 1px solid rgba(36, 42, 55, 0.5);
  margin: 0 auto 12px;
}

.text:nth-child(4) {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;
  text-align: center;
  color: var(--secondary-text-color);
}

@media screen and (min-width: 768px) {
  .container {
    padding: 98px 0 110px;
    width: 397px;
    margin: auto;
  }

  .text:nth-child(2) {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 1.58;
  }

  hr {
    width: 150px;
  }

  .text:nth-child(4) {
    font-size: 20px;
    line-height: 1.2;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    padding: 206px 0 352px;
  }
}
